import React, { Component, useState } from 'react';
import './App.css';

function App() {
  const [hdm_count,sethdm] = useState(0);
  const [polm_count,setpolm] = useState(0);
  const [chips_count, setchips] = useState(0);
  const [single_pol,setsingpol] = useState(0);
  const [sd,setsd] = useState(0);

  /*
  */
  return (
    <div className="App">
      <h1>Sales</h1>
        <center><h4>HOT DOGS</h4>
        <button onClick={()=>setsd(sd-1)}>-1</button>
        <button onClick={()=>setsd(sd + 1)}>+1: </button>
        <p>{sd}</p>
      <h4>POLISH</h4>
        <button onClick={()=>setsingpol(single_pol-1)}>-1</button>
        <button onClick={()=>setsingpol(single_pol+1)}>+1</button>
        <p>{single_pol}</p>
      <h4>HOT DOG MEAL</h4>
        <button onClick={()=>sethdm(hdm_count-1)}>-1</button>
        <button onClick={()=>sethdm(hdm_count+1)}>+1</button>
        <p>{hdm_count}</p>
      <h3>POLISH MEAL</h3>
        <button onClick={()=>setpolm(polm_count-1)}>-1</button>
        <button onClick={()=>setpolm(polm_count+1)}>+1</button>
        <p>{polm_count}</p>
      <h4>Chips/Pop</h4>
      <button onClick={()=>setchips(chips_count-1)}>-1</button>
      <button onClick={()=>setchips(chips_count+1)}>+1</button>
      <p>{chips_count}</p>
      <h3>Revenue: ${(polm_count * 8)+(single_pol*6)+(sd*4)+(hdm_count*6)+(chips_count)}</h3>
      </center>
    </div>
  );

}

export default App;



